import React from 'react';

import BlockCTA from './BlockCTA';
import CaseStudyCard from './CaseStudyCard';

import { fonts } from '../constants';
import './fonts.css';

export default function CaseStudyCards({ title, items = [], showCTA = true }) {
  return (
    <div className="case-studies__container">
      {title && <h4 className="case-studies__title">{title}</h4>}
      {items.length > 0 && (
        <div className="case-studies__grid">
          {items.map((item, index) => (
            <CaseStudyCard
              key={index}
              title={item.title}
              description={item.description}
              link={item.link}
              image={item.image}
            ></CaseStudyCard>
          ))}
        </div>
      )}
      {showCTA && (
        <BlockCTA
          align="center"
          primaryButtonTo="/case-studies"
          primaryButtonText="View all case studies"
        />
      )}
      <style jsx global>{`
        .case-studies__title {
          font-family: ${fonts.gilroy};
          text-align: center;
          font-size: 48px;
          font-weight: 600;
          margin: 80px 0px 24px;
        }

        .case-studies__grid {
          display: grid;
          grid-template-columns: repeat(auto-fit, minmax(270px, 1fr));
          grid-gap: 2rem 1rem;
          margin: 0 auto 30px;
          padding: 0 50px;
          max-width: 70rem;
        }
      `}</style>
    </div>
  );
}
