import React from 'react';
import { graphql } from 'gatsby';
import { GatsbySeo } from 'gatsby-plugin-next-seo';

import Layout from '../../components/Layout';
import Banner from '../../components/Banner';
import Container from '../../components/Container';
import Footer from '../../components/Footer';
import SpaceDivider from '../../components/SpaceDivider';
import BlockHeaderNoImage from '../../components/BlockHeaderNoImage';
import BlockCaseStudyCards from '../../components/BlockCaseStudyCards';

import { edgesByName } from '../../utils/gql';

export const pageQuery = graphql`
  {
    biggerBuyersImage: file(name: { eq: "bigger-buyers" }) {
      childImageSharp {
        fluid(maxWidth: 520) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    caseStudyImages: allFile(
      filter: { relativeDirectory: { eq: "case-study" } }
    ) {
      edges {
        node {
          name
          childImageSharp {
            fluid {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    }
    hyperikonLedTubes: contentfulAsset(title: { eq: "hyperikon-led-tubes" }) {
      title
      fluid {
        ...GatsbyContentfulFluid
      }
    }
    viceGolfBrandImage: contentfulAsset(
      title: { eq: "vice-golf-brand-image" }
    ) {
      title
      fluid {
        ...GatsbyContentfulFluid
      }
    }
    site {
      siteMetadata {
        siteUrl
      }
    }
  }
`;
export default function caseStudiesPage({ data }) {
  const caseStudyImages = edgesByName(data.caseStudyImages);
  const title = 'Ð¡ustomer Success Stories';
  const description =
    'Learn how our customers have used Resolve to simplify their accounts receivable, improve cashflow, reduce risk, and grow sales.';

  return (
    <Layout>
      <GatsbySeo
        title={title}
        description={description}
        openGraph={{
          url: `${data.site.siteMetadata.siteUrl}/customers/`,
          title: `${title} | Resolve`,
          description: `${description}`,
        }}
      />
      <Banner />
      <Container>
        <BlockHeaderNoImage
          pretitle="CUSTOMER STORIES"
          title="You're in great hands"
          desc={
            <>
              Join innovative companies in choosing Resolve to simplify their
              accounts receivable, improve cashflow, reduce risk, and grow
              sales.
            </>
          }
          primaryButtonText="Get started"
          primaryButtonTo="/contact-sales"
        />
        <SpaceDivider height={100} />
        <BlockCaseStudyCards
          items={[
            {
              title: 'Fire and life safety testing equipment supplier',
              description:
                'SDi Fire uses Resolve to unlock working capital and increase margins',
              link: '/customers/sdi-fire/',
              image: caseStudyImages['fire-check'],
            },
            {
              title: 'LED lighting manufacturer',
              description:
                'How Hyperikon multiplied their b2b sales and reduced their deal closing time with Resolve',
              link: '/customers/hyperikon/',
              image: data.hyperikonLedTubes.fluid,
            },
          ]}
          showCTA={false}
        />
      </Container>
      <Footer />
    </Layout>
  );
}
